 	export default {
 		data() {
 			return {
				itemList:[],
				shopid: 0
 			}
 		},
 		onLoad:function(option){
 			this.shopid = option.shopid
 		},
 		onShow:function(){
 			var that = this
 			uni.request({
 				url: this.$bashUrl+ '/index.php/home/Api/table_list/',
 				method: 'POST',
 				data:{
					id: this.shopid
 				},
 				success:(res=>{
					console.log(res.data)
 					that.itemList = res.data
 				})
 			})			
 		},
 		methods: {
			Toaddtable:function(){
				uni.navigateTo({
					url: '/pages/BusinessSide/addTable/addTable?shopid=' + this.shopid,
				})
			},
			Deletetable:function(id){
				var that = this
				uni.request({
					url: this.$bashUrl+ '/index.php/home/Api/del_table/',
					method: 'POST',
					data:{
						id: id
					},
					success:(res=>{
						uni.showToast({
							title: '删除成功',
							icon:"none"
						});
						// 重新请求数据
						uni.request({
							url: this.$bashUrl+ '/index.php/home/Api/table_list/',
							method: 'POST',
							data:{
								id: that.shopid
							},
							success:(res=>{
								
								that.itemList = res.data
							})
						})
					})
				})
			},
			changeStats:function(stats,id){
				// 修改桌子状态
				var map = {'0':1,'2':2}
				var typeId = map[stats]
				uni.request({
					url: this.$bashUrl+ '/index.php/home/Api/use_table/',
					method: 'POST',
					data:{
						id: id,
						type: typeId 
					},
					success:(res=>{
						var that = this
						uni.request({
							url: this.$bashUrl+ '/index.php/home/Api/table_list/',
							method: 'POST',
							data:{
								id: this.shopid
							},
							success:(res=>{
								that.itemList = res.data
							})
						})			
						uni.showToast({
							title: '状态修改成功',
							icon:'none'
						})
					})
				})
			}
 		}
 	}
 